import React, { useContext } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Copyright from '../../../components/Copyright';
import ItemVenda from './components/item-venda';
import api from '../../../services/api';
import { SharedSnackbarContext } from '../../../providers/snackbar-provider';

const useStyles = makeStyles((theme) => ({
    '@global': {
        ul: {
            margin: 0,
            padding: 0,
            listStyle: 'none',
        },
    },
    appBar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbar: {
        flexWrap: 'wrap',
    },
    toolbarTitle: {
        flexGrow: 1,
    },
    link: {
        margin: theme.spacing(1, 1.5),
    },
    heroContent: {
        padding: theme.spacing(8, 0, 6),
    },
    footer: {
        borderTop: `1px solid ${theme.palette.divider}`,
        marginTop: theme.spacing(8),
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(3),
        [theme.breakpoints.up('sm')]: {
            paddingTop: theme.spacing(6),
            paddingBottom: theme.spacing(6),
        },
    },
}));

export default function Loja() {
    const classes = useStyles();

    const { openSnackbar } = useContext(SharedSnackbarContext);
    const [produtos, setProdutos] = React.useState([]);

    const [pagina, setPagina] = React.useState(0);
    const [totalRegistros, setTotalRegistros] = React.useState(0);
    const limit = 40;


    React.useEffect(() => buscarProdutos(), [])

    const buscarProdutos = () => {

        api.get('loja/produtos', {
            params: {
                totalRegistros: totalRegistros,
                pagina: pagina,
                limit: limit
            }
        }).then(response => {

            const retorno = response.data;

            setProdutos(retorno.content);
        })
            .catch((error) => {

                let response = error.response;

                if (response && response.status === 400) {

                    openSnackbar(response.data.mensagem, 'warning');
                } else {

                    openSnackbar('Ocorreu um erro não tratado pelo servidor.', 'error');
                }
            });
    }

    return (
        <React.Fragment>
            <CssBaseline />
            <AppBar position="static" color="default" elevation={0} className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h6" color="inherit" noWrap className={classes.toolbarTitle}>
                        Company name
          </Typography>
                    <nav>
                        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
                            Features
            </Link>
                        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
                            Enterprise
            </Link>
                        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
                            Support
            </Link>
                    </nav>
                    <Button href="#" color="primary" variant="outlined" className={classes.link}>
                        Login
          </Button>
                </Toolbar>
            </AppBar>

            <Container maxWidth="sm" component="main" className={classes.heroContent}>
            </Container>

            <Container maxWidth="md" component="main">
                <Grid container spacing={5} alignItems="flex-end">
                    {produtos.map((row) => <ItemVenda row={row} />)}
                </Grid>
            </Container>

            <Container maxWidth="md" component="footer" className={classes.footer}>
                <Copyright />
            </Container>

        </React.Fragment>
    );
}